/*---Navbar---*/
* {
	font-family: 'Montserrat', sans-serif;

}

html {
	scroll-behavior: smooth;
	overflow-x: hidden;

}

.navbar-nav a {
	font-size: 18px;
	text-transform: uppercase;
	font-weight: bold;
}

.navbar-light .navbar-brand {
	color: #fff;
	font-size: 25px;

	font-weight: bold;
	letter-spacing: 2px;
}

.navbar-light .navbar-brand:focus,
.navbar-light .navbar-brand:hover {
	color: #fff;
}

.navbar-light .navbar-nav .nav-link {
	color: #fff;
	position: relative;
}


.navbar .nav-item .nav-link::before {
	color: #fff;
}

.navbar .nav-item .nav-link::after {
	content: '';
	position: absolute;
	width: 100%;
	transform: scaleX(0);
	height: 2px;
	bottom: 0;
	left: 0;
	background-color: #fff;
	transform-origin: bottom right;
	transition: transform 0.25s ease-out;

}

.navbar .nav-item .nav-link:hover::after {
	transform: scaleX(1);
	transform-origin: bottom left;

}

.navbar .nav-item .nav-link:hover {
	color: #fff;
}

.navbar-toggler {
	padding: 1px 5px;
	font-size: 18px;
	line-height: 0.3;
	background: #fff;

}

.bg-light {
	background-color: transparent !important;
}


.cur {
	font-family: 'Satisfy', cursive;
	color: #ffffff;
	font-size: 40px;
	display: inline-block;
	font-size: 25px;
}


.navbar-brand {
	line-height: 1;

}

.bg {
	background-color: #226613;

}

/*--Carousel---*/

.carousel-item {
	height: 100vh;
	min-height: 300px;
}

.carousel-caption {
	bottom: 220px;
	z-index: 2;
}

.carousel-caption h5 {
	font-size: 45px;
	text-transform: uppercase;
	letter-spacing: 2px;
	margin-top: 25px;
}

.carousel-caption p {
	width: 60%;
	margin: auto;
	font-size: 18px;
	line-height: 1.9;
}

.carousel-caption a {
	text-transform: uppercase;
	text-decoration: none;
	background: transparent;
	padding: 5px 20px;
	display: inline-block;
	color: #fff;
	margin-top: 15px;
	border-radius: 5px;
	background-color: rgb(220, 9, 41);

}

.carousel-caption a:hover {
	background-color: #ffffff;
	color: #21c119;

}

.carousel-inner:before {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: rgba(0, 0, 0, 0.35);
	z-index: 1;
}

.w-100 {
	height: 100vh;
}

@media only screen and (max-width: 767px) {
	.navbar-nav {
		text-align: center;
	}

	.carousel-caption {
		bottom: 232px;
	}

	.carousel-caption h5 {
		font-size: 17px;
	}

	.carousel-caption a {
		padding: 10px 15px;
		font-size: 15px;
	}


}

